                      <div>修改历史</div>
                    </el-card>
                  </el-col>
                  <el-col :span="9">
                    <el-card class="box-card">
                      <p>项目进度</p>
                      <el-progress
                        :text-inside="true"
                        :stroke-width="24"
                        :percentage="100"
