                    </el-card>
                    <el-card class="box-card">
                      <p>&emsp;任务名称</p>
                      <p>&emsp;所属项目</p>
                      <p>&emsp;迭代编码</p>
                      <p>父任务编码</p>
                      <p>责任田编码</p>
                      <p>&emsp;任务级别</p>
                      <p>&emsp;任务类型</p>
                      <p>任务优先级</p>
                      <p>&emsp;创建时间</p>
                      <p>&emsp;开始时间</p>
                      <p>&emsp;计划用时</p>
                      <p>&emsp;&emsp;创建人</p>
                    </el-card>
                  </el-col>
                </div>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <CutPage v-on:currentPage="currentPage" style="margin-top:20px"></CutPage>
      </el-card>
      <div class="rightBox">
        <el-card class="box-card rightBottonBox">
          <el-col :span="10">
            <ul class="leftUl">
              <li>项目编码：</li>
              <li>项目名称：</li>
              <li>项目类型：</li>
              <li>商务类型：</li>
              <li>&emsp;业务群：</li>
              <li>&emsp;业务线：</li>
              <li>归属地区：</li>
              <li>客户接口人：</li>
            </ul>
          </el-col>
