              <li>创建人：</li>
              <li>项目计划启动时间：</li>
              <li>项目计划结束时间：</li>
              <li>项目实际启动时间：</li>
              <li>项目实际结束时间：</li>
              <li>项目状态：</li>
              <li>交付经理：</li>
              <li>SPM：</li>
              <li>PM：</li>
              <li>QA：</li>
            </ul>
